.projects {
  /* flex: 1; */
  padding: 2rem;
  padding-top: 4rem;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin-top: 70px;
  width: 100%;
  color: var(--text-dark);
  user-select: none;
  min-height: 100vh;
}

.add_project_btn {
  position: fixed;
  top: 5rem;
  right: 2rem;
  background-color: var(--theme-blue);
  padding: 0.5rem 2rem;
  color: var(--theme-light);
  z-index: 1;
  font-weight: 700;
}

.project-cards {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.empty-project {
  position: absolute;
  height: 80vh;
  width: calc(100% - 4rem);
  margin-top: 4rem;
  font-size: 3rem;
  text-align: center;
}

@media screen and (max-width: 765px) {
  .add_project_btn {
    right: 0.75rem;
  }

  .project-cards {
    flex-wrap: wrap;
  }
}
